import React from 'react';
import Header from '../Header/Header';
import {NavLink} from "react-router-dom";
import Alert from "../Alert/Alert";

const Orders = (props) => {

    return(
        <div className="home-page site-wrap">
            <Header />
            <div className="container">
                <h1 className="my-3">Накладные</h1>
                <div className="list-group">
                    {props.orders.orders ?
                        props.orders.orders.map(
                order =>
                    <NavLink to={'/order/'+ order.consignationId}
                             key={order.consignationId}
                             className="list-group-item d-flex justify-content-between align-items-center list-group-item-action">
                        <b>{order.marketName}</b>
                        <small>{order.orderCreateDate}</small>
                            <span className="badge badge-primary badge-pill">{order.orderItemsCount}</span>
                            <span className="badge badge-primary badge-pill">{order.orderItemsCountDone}</span>
                            <span className="badge badge-danger badge-pill">{order.amount3}</span>
                            {order.status>0 ?
                                <span className="ti-check-box text-success"></span> :
                                <span className="ti-clipboard text-danger"></span>}
                    </NavLink>



                ) : <Alert text={'Накладные не найдены. Обратитесь к менеджеру.'} alertType={'danger'}/>}
                    </div>
            </div>
        </div>

    )
};


export default Orders;
